import React, { Component } from 'react'
import HeaderBurgerMenu from './HeaderBurgerMenu'
import logoTissot from '../assets/logo-tissot.svg'
import HeaderSearchbox from './HeaderSearchbox'
import HeaderSearchboxMobile from './HeaderSearchboxMobile'
import HeaderHelp from './HeaderHelp'
import HeaderApp from './HeaderApp'
import HeaderAccount from './HeaderAccount'
import HeaderMySpace from './HeaderMySpace'

class Header extends Component {
    state = {
        classBorder: ''
    };

    componentDidMount() {
        document.addEventListener('scroll', () => {
            const borderScroll = window.scrollY < 60 ? '' : 'headerScroll';

            this.setState({classBorder: borderScroll});
        })
    }

    render() {
        return (
            <header className={`mainHeader flex-items-center ${this.state.classBorder}`}>
                <div className="sidebarHeader flex-items-center">
                    <HeaderBurgerMenu />
                    <img src={logoTissot} alt="Edition Tissot" className="logo"/>
                    <div className="appType">Documentation</div>
                </div>
                <HeaderSearchbox />
                <HeaderSearchboxMobile />
                <HeaderHelp />
                <HeaderApp />
                <HeaderAccount />
                <HeaderMySpace />
            </header>
        )
    }
}

export default Header;