import React from 'react'
import Header from '../components/Header'
import AppMain from '../components/AppMain'
import Sommaire from '../data/sommaire.json'
import ListUC from '../components/ListUC'
import ListChapUC from '../components/ListChapUC'
import { useParams } from 'react-router-dom'
import Sidebar from '../components/Sidebar'
import ContentNav from '../components/ContentNav'


const PartiePage = () => {

    const {partPath} = useParams()
    const {docuRef} = useParams()
    const docReference = Sommaire.findIndex( i => i.docRef === docuRef)
    const docSommaire = Sommaire[docReference].parties
    const partie = docSommaire.find(({ path }) => path === partPath )
    const nbpart = docSommaire.findIndex( i => i.partie === partie.partie)

    let sidebarList = {
        sidebarDocRef: docReference,
        sidebarDocSom: docSommaire
    }

    let isChapitres
    let chapPath = {
        docuRef: docuRef,
        partPath: partPath,
        chapList: partie.chapitres
    }
    let ucPath = {
        docuRef: docuRef,
        partPath: partPath,
        ucList: partie.uc
    }
    docSommaire.some(el => {
        if(el.hasOwnProperty('chapitres')) {
            isChapitres = 
                <ListChapUC {...chapPath}/>
        } else {
            isChapitres = 
                <div className="row blockUC">
                    <div className="col-xxl-8 col-xl-8 col-lg-9 col-md-10 col-sm-11">
                        <ListUC {...ucPath} />
                    </div>  
                </div>
        }
    })

    return (
        <div>
            <Header />
            <AppMain className={"documentation"}>
                <Sidebar {...sidebarList}/>
                <div className="colContent">
                    <ContentNav />
                    <div className="row">
                        <div className="col-xxl-8 col-xl-8 col-lg-9 col-md-10 col-sm-11">
                            <div className="partHeading">
    <span>{String((nbpart+1)).padStart(2, '0')}</span>
                                <h1>{partie.partie}</h1>
                            </div>
                        </div>
                    </div>
                    {isChapitres}
                </div>
            </AppMain>
        </div>
    );
}

export default PartiePage;