import React, {Component} from 'react'

class SearchboxMobile extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        show: false
    }
    toggle = () => this.setState((currentState) => ({show: !currentState.show}));
    render() {
        return (
            <div className={`searchBoxMobile ${this.state.show ? "open" : "close"}`} onClick={this.toggle}>
                <i class="fas fa-search"></i>
                {this.state.show &&
                    <div className="searchBoxPanel">
                        <form>
                            <input type="search" placeholder="Rechercher dans la documentation"/>
                            <button type="submit"></button>
                        </form>
                    </div>
                }
            </div>
        );
    }
}

export default SearchboxMobile;