import React, { Component } from 'react'
import Accordion from './Accordion/Accordion'
import Sommaire from '../data/sommaire.json'

const SommaireBlockCollapsible = () => {
    return (
        <div>
            {Sommaire.map((doc, i) =>
                <div className={`summaryDoc ${doc.docRef}`} key={i}>
                    <Accordion className={'summaryItem'}>
                        {doc.parties.map(({partie, chapitres}) => (
                            <Accordion.Item key={partie}>
                                <Accordion.Collapsed id={partie} className={`summaryPart flex-items-center`}>
                                    <i className="fal fa-plus-square"></i>
                                    {partie}
                                </Accordion.Collapsed>
                                <Accordion.Expanded className={`summaryList flex-items-center`}>
                                    <Accordion className={'summaryItem'}>
                                        {chapitres.map(({chapTitle, uc}) => (
                                            <Accordion.Item key={chapTitle}>
                                                <Accordion.Collapsed id={chapTitle} className={`flex-items-center`}><i class="fal fa-plus-square"></i>{chapTitle}</Accordion.Collapsed>
                                                <Accordion.Expanded className={'summaryList summaryUC'}>
                                                        {uc.map((ucs, i) => 
                                                            <a className="summaryItem linkUc">{ucs.ucTitle} <span className="refInternet">| {ucs.ucRef}</span></a>
                                                        )}
                                                </Accordion.Expanded>
                                            </Accordion.Item>
                                        ))}
                                    </Accordion>
                                </Accordion.Expanded>
                            </Accordion.Item>
                        ))}
                    </Accordion>
                </div>                
            )}
        </div>
    )
}

export default SommaireBlockCollapsible;