import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'
import Sommaire from '../data/sommaire.json'

const SommaireBlock = () => {
        return(
            <div className="sommaire">
                {Sommaire.map((doc, i) =>
                <div className={`summaryDoc ${doc.docRef}`} key={i}>
                    <h2>Sommaire</h2>
                    <ul className="whiteBlock">
                        {doc.parties.map((parties, i) =>
                        <li key={i} className="listPart">
                            <NavLink to={`/${doc.docRef}/${parties.path}`} className="flex-items-center">
                                <span className="nbPart">{String((i+1)).padStart(2, '0')}</span>
                                <div>{parties.partie}</div>
                            </NavLink>
                        </li>
                        )}
                    </ul>
                </div>                
                )}
            </div>
        );
}

export default SommaireBlock;