import React, {Component} from 'react'
import {HashRouter as Router, Route, Switch} from 'react-router-dom'
import './css/flexboxgrid2.css'
import './css/default.css'
import './css/header.css'
import './css/sidebar.css'
import './css/appmain.css'
import './css/mobile.css'
import './css/temp.css'

import Homepage from './pages/index'
import ModelesPage from './pages/modeles'
import ProceduresPage from './pages/procedures'
import PartiePage from './pages/partie'
import UC from './pages/uc'

class App extends Component {
  render() {
    return (
      <Router>
        <Switch>
          <Route exact path="/" component={ Homepage } />
          <Route exact path="/glp/modeles" component={ ModelesPage }/>
          <Route exact path="/glp/procedures-lumio" component={ ProceduresPage }/>
          <Route exact path="/:docuRef/:partPath" component={ PartiePage } />
          <Route exact path="/uc" component={UC}/>
        </Switch>
      </Router>
    );
  }
}

export default App;
