import React, { Component } from 'react'
import Accordion from './Accordion/Accordion'
import Sommaire from '../data/sommaire.json'
import { NavLink } from 'react-router-dom'


class SommaireBlockCollapsible2 extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        show: false
    }
    toggle = () => this.setState((currentState) => ({show: !currentState.show}));
    render() {
        return (

                <div>
                    {Sommaire.map((doc, i) =>
                        <div className={`summaryDoc ${doc.docRef}`} key={i}>
                            {doc.parties.map((parties, i) => (
                                <ul>
                                    <li onClick={this.toggle}>
                                        <NavLink exact to="/uc">{parties.partie}</NavLink>    
                                    </li>
                                </ul>
                            ))}
                        </div>                
                    )}
                </div>
        );
    }
}

export default SommaireBlockCollapsible2;